<template>
  <div class="page graybg page-orders">
    <van-tabs v-model="tabActive" border offset-top="12.267vw" sticky>
      <van-tab
        v-for="(v, k) in tabs"
        :key="k"
        :name="k"
        :title="v.title"
        :to="`/orders/${k}`"
        replace
      >
        <template #title>
          <van-dropdown-menu>
            <van-dropdown-item
              v-model="listType"
              :options="filteredListTypeOptions"
              :disabled="tabActive === 'filter'"
              @change="onDropdownItemChange"
            />
          </van-dropdown-menu>
        </template>
        <van-pull-refresh v-model="v.loading" @refresh="onRefresh">
          <van-list
            v-model="v.loading"
            :ref="k"
            :error-text="v.errorText"
            :error.sync="v.error"
            :finished="v.finished"
            :finished-text="v.finishedText"
            @load="chooseLoadList"
          >
            <template v-for="(item, index) in v.list">
              <CryptoItem v-if="isFilterDisabled" :key="index" :item-data="item" />
              <TransactionItem v-else :key="index" :dt="item" />
            </template>
          </van-list>
        </van-pull-refresh>
      </van-tab>
      <van-tab name="filter" title="筛选" :disabled="isFilterDisabled">
        <template #title>
          筛选
          <van-icon name="filter-o" />
        </template>
        <div class="filters">
          <van-checkbox-group v-model="form.status">
            <van-checkbox
              v-for="v in statusList"
              :key="v.key"
              :name="v.key"
              class="btn"
              label-disabled
            >
              <template #icon>
                <div class="btn">
                  {{ v.name }}
                </div>
              </template>
            </van-checkbox>
          </van-checkbox-group>
        </div>
        <van-cell class="btns">
          <van-button class="cancel" type="danger" @click="resetFilters"> 重置 </van-button>
          <van-button class="confirm" type="primary" @click="confirmFilters"> 确定 </van-button>
        </van-cell>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { TransactionItem } from "../home/components";
import { myOrderList } from "@/apis/market";
import { getOrderList, getOrderStatus } from "@/apis/crypto";
import CryptoItem from "./components/CryptoItem";
import store from "./store";
export default {
  components: { TransactionItem, CryptoItem },
  data() {
    return store.state;
  },
  computed: {
    isFilterDisabled() {
      if (this.tabsActiveForListType === "normal") {
        return this.listType !== "all" && this.listType !== "buyer" && this.listType !== "seller";
      }else{
        return true;
      }
      
    },
    filteredListTypeOptions() {
      return this.listTypeOptions.filter((item) => {
        if (this.tabsActiveForListType === "normal") {
          return item.value === "buyer" || item.value === "seller" || item.value === "all";
        } else {
          return ["TronDeposit","TronTransferBusiness","DepositTransferIn","DepositTransferOut","BusinessTransferVippayOut","all"].includes(item.value);
        }
      });
    },
  },
  watch: {
    "form.status"(newVal) {
      localStorage.setItem("ORDERS_FILTERS", JSON.stringify(newVal));
    },
    tabActive(_, oldValue) {
      if (oldValue === "filter") return;
      this.oldTabActive = oldValue;
    },
    tabsActiveForListType(newVal) {
      if( newVal === "block" ){
        this.tabActive = "list";
      }
      this.listType = "all";
      this.onDropdownItemChange();
    },
  },
  created() {
    this.getOrderStatus();
    this.$root.updateUnReadCount();
    this.$root.$emit("root:set-topbar", ["customer-service"]);
    this.$root.$emit("root:set-layout", { bottom: true });
    try {
      this.form.status = JSON.parse(localStorage.getItem("ORDERS_FILTERS")) || [];
    } catch (error) {
      this.form.status = [];
      localStorage.removeItem("ORDERS_FILTERS");
    }
  },
  methods: {
    onDropdownItemChange() {
      this.onRefresh();
    },
    onRefresh() {
      this.tabs[this.oldTabActive] = {
        loading: true,
        error: false,
        finished: false,
        errorText: "加载失败，点击重试",
        finishedText: "暂无更多数据",
        list: [],
        page: 1,
        pageSize: 20,
      };
      this.chooseLoadList();
    },
    chooseLoadList(A) {
      if (this.isFilterDisabled) {
        this.loadCryptoList();
      } else {
        this.loadList();
      }
    },
    async loadCryptoList() {
      const { ok, data, msg } = await getOrderList({
        page: this.tabs[this.oldTabActive].page,
        pageSize: this.tabs[this.oldTabActive].pageSize,
        type: this.tabsActiveForListType !== "normal" && this.listType==="all"?"":this.listType,//区块订单 全部 不传type
      });
      if (ok) {
        if (
          this.tabs[this.oldTabActive].page * this.tabs[this.oldTabActive].pageSize >=
          data.total
        ) {
          this.tabs[this.oldTabActive].finished = true;
        }
        this.tabs[this.oldTabActive].loading = false;
        this.tabs[this.oldTabActive].list.push(...data.list);
        this.tabs[this.oldTabActive].page += 1;
      } else {
        this.tabs[this.oldTabActive].error = true;
        this.tabs[this.oldTabActive].errorText = msg;
      }
    },
    loadList() {
      myOrderList(
        this.$tls.processParams(
          {
            page: this.tabs[this.oldTabActive].page,
            pageSize: this.tabs[this.oldTabActive].pageSize,
            identity: this.listType === "all" ? "" : this.listType,
            status: this.form.status,
          },
          ["shake"]
        ),
        this.tabsActiveForListType === "block"
      ).then(({ ok, msg, data }) => {
        if (ok) {
          this.tabs[this.oldTabActive].loading = false;
          this.tabs[this.oldTabActive].list.push(...data.list);
          if (
            this.tabs[this.oldTabActive].page * this.tabs[this.oldTabActive].pageSize >=
            data.total
          ) {
            this.tabs[this.oldTabActive].finished = true;
            return;
          }
          this.tabs[this.oldTabActive].page += 1;
        } else {
          this.tabs[this.oldTabActive].error = true;
          this.tabs[this.oldTabActive].errorText = msg;
        }
      });
    },
    confirmFilters() {
      this.onRefresh();
      this.tabActive = this.oldTabActive;
    },
    resetFilters() {
      this.form.status = [];
      localStorage.removeItem("ORDERS_FILTERS");
      this.onRefresh();
      this.tabActive = this.oldTabActive;
    },
    async getOrderStatus() {
      const list = window.sessionStorage.getItem("ORDER_STATUS");
      if (list) {
        try {
          const _list = JSON.parse(list);
          this.statusList = _list;
          return;
        } catch (error) {
          console.error(error);
        }
      }
      const { ok, data, msg } = await getOrderStatus();
      if (ok) {
        this.statusList = data;
        window.sessionStorage.setItem("ORDER_STATUS", JSON.stringify(data));
      } else {
        this.$toast(msg);
      }
    },
  },
};
</script>

<style lang="scss">
.page.page-orders {
  overflow-y: auto;

  .van-dropdown-menu__title {
    &::after {
      right: 0px;
    }
  }

  .van-dropdown-menu__item {
    &--disabled {
      .van-dropdown-menu {
        &__title {
          color: #333333;
        }
      }
    }
  }

  .filters {
    .van-checkbox-group {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;

      .van-checkbox {
        margin-top: 10px;

        .btn {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          padding: 0 10px;
          width: 111.5px;
          box-sizing: border-box;
        }
      }
    }
  }

  .btns {
    padding-top: 50px;

    .van-cell__value {
      display: flex;
      justify-content: space-around;

      .van-button {
        width: 168px;
        height: 40px;
        border-radius: 5px;
        border: none;

        &.cancel {
          color: #333;
          background-color: #f0f0f0;
        }

        &.confirm {
          color: #fff;
          background-color: #6f5bfe;
        }
      }
    }
  }
}
</style>
